
body {
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
  color: #000;
}
li {
  list-style: none;
}
input {
  outline: none;
}

.loginMain {
  width: 100vw;
  height: 100vh;
  background: -webkit-linear-gradient(45deg, skyblue, pink);
  overflow: hidden;
}

.loginBox {
  margin: 160px auto;
  width: 360px;
  height: 240px;
}
.loginBox .title {
  color: #fff;
  font-size: 32px;
  font-weight: 700;
  text-align: center;
}
.loginBox input {
  display: block;
  width: 240px;
  height: 36px;
  padding: 0px 10px;
  border: 0px;
  border-radius: 5px;
  margin: 20px auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 16px;
  color: #fff;
  background-color: rgba(80, 80, 80, 0.3);
}
button {
  display: block;
  width: 240px;
  height: 36px;
  padding: 0px 10px;
  border: 0px;
  border-radius: 5px;
  margin: 20px auto;
  font-size: 16px;
  color: #eee;
  background-color: #f26934;
  outline: none;
  cursor: pointer;
}
button:hover {
  background-color: #f05418;
}
